import React from 'react';
import Sets from './pages/sets'
import Layers from './pages/layers'
import LayerItems from './pages/layer-items/LayerItems'
import LayerItemsAdd from './pages/layer-items/LayerItemsAdd'
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import "react-tabs/style/react-tabs.css";
import './App.scss';
import { connect } from 'react-redux'


export default class App extends React.Component {

  render() {
    return (
      <div className="App">
        <Router>
          <Switch>
            <Route path="/avatar-platform-admin/" exact component={Sets} />
            <Route path="/avatar-platform-admin/:setName" exact component={Layers} />
            <Route path="/avatar-platform-admin/:setName/add" component={LayerItemsAdd} />
            <Route path="/avatar-platform-admin/:setName/:id" component={LayerItems} />
          </Switch>
        </Router>
      </div>
    );
  }
}

