import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './layer-items.scss'
import Paper from '@material-ui/core/Paper';
import { getLayerItems, postLayerItemsImages, addLayer } from '../../services/backend'
import Dropzone from 'react-dropzone'
import { Link } from "react-router-dom";
import Spinner from "../../components/spinner";
import Colors from './colors';
import Images from './images'
import Form from './form'

export default function LayerItemsAdd (props) {

  const [imagesData, setImagesData] = useState({});
  const [isLoading, setIsLoading] = useState(false);


  function onImageSubmit(acceptedFiles) {
    let fileToSend = acceptedFiles[0];
    setIsLoading(true);
    postLayerItemsImages(props.match.params.setName, props.match.params.id, fileToSend)
      .then((response) => {
        setIsLoading(false);
        setImagesData(response.data[0]);
      })
  }

  function onAddLayer(data) {
    return addLayer(props.match.params.setName, data).then((response) => {
      props.history.push('/' + props.match.params.setName + '/' + response.data.id)
    })
  }

  return (
    <div className='LayerItems'>
      <Link to='../'>Back to Layers</Link>
      <h2>Add</h2>
      <Form
        id={props.match.params.id}
        data={imagesData}
        onSubmit={onAddLayer}
      />
    </div>
  )
}
